.aboutmecontainer {
    /* background: url('/images/img-home.jpg') center center/cover no-repeat; */
    background-image: linear-gradient(rgba(5, 5, 5, 0.828) 20%,#ac3ac6, white 150%);
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;
    padding: 12% 12%;
    margin: 0px;
    overflow-x: hidden;
    color: white;
  }